import React from 'react';

const Modal = ({closeModal, active, children}) => {
    return (
       <>
           <div className={`modal ${ active ? ' modal_active' : ''}`}>
                <div className={`modal__content ${ active ? ' modal__content_active' : ''}`}>
                    <div className="modal__close" onClick={closeModal}>&times;</div>
                    {children}
                </div>
            </div>
       </>
    )
}


export default Modal;